/* ---------- Small Header ---------- */

.awssld__content {
  background-color: white !important;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.awssld__container figure,
.awssld__content,
.awssld__box {
  display: block;
  position: absolute;
  width: 100%;
  height: 100% !important;
  top: 0;
  left: 0;
}

.awssld__controls button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: var(--control-button-width);
  height: var(--control-button-height);
  position: absolute;
  z-index: 10;
  top: calc(15% - (0.5 * var(--control-button-height)));
  border: none;
  background-color: var(--control-button-background);
  color: #fff;
  cursor: pointer;
}

.awssld__container {
  display: block;
  width: 100%;
  height: 10%;
  padding-bottom: 20% !important;
}

.awssld__bullets .awssld__bullets--active {
  background: #8a4baf !important;
  outline: 2px solid #dec4ec !important;
  outline-offset: 3px !important;
  width: 10px !important;
  height: 10px !important;
}

.awssld__bullets button {
  background: #d9d7e0 !important;
  margin-right: 40px !important;
}

.awssld__bullets button {
  padding: 0;
  display: block;
  width: 12px !important;
  height: 12px !important;
  margin: 5px;
  border-radius: 50%;
  background: var(--control-bullet-color);
  text-indent: -9999px;
  overflow: hidden;
  cursor: pointer;
  border: none;
  transition: background-color 0.175s ease-out,
    -webkit-transform 0.225s cubic-bezier(0.8, 1.35, 0.75, 1.45);
  transition: transform 0.225s cubic-bezier(0.8, 1.35, 0.75, 1.45),
    background-color 0.175s ease-out;
  transition: transform 0.225s cubic-bezier(0.8, 1.35, 0.75, 1.45),
    background-color 0.175s ease-out,
    -webkit-transform 0.225s cubic-bezier(0.8, 1.35, 0.75, 1.45);
}

.runing-animation {
  -webkit-animation: imageSlider linear 16s infinite;
  animation: imageSlider linear 16s infinite;
}

.runing-animation:hover {
  -webkit-animation-play-state: paused;
  animation-play-state: paused;
}

.runing-animation img {
  max-width: inherit;
}

@-webkit-keyframes imageSlider {
  0% {
    -webkit-transform: translateX(10%);
    transform: translateX(10%);
  }

  100% {
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
  }
}

@keyframes imageSlider {
  0% {
    -webkit-transform: translateX(10%);
    transform: translateX(10%);
  }

  100% {
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
  }
}

