/* #Mega Menu Styles
   */
.mega-menu {
  display: none;
  left: 0;
  position: absolute;
  text-align: left;
  width: 50%;
}

/* #hoverable Class Styles
   */
.hoverable {
  position: static;
}

.hoverable:hover .mega-menu {
  display: block;
}

/* ---------- Small Header ---------- */

.awssld__content {
  background-color: white !important;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.awssld__container figure,
.awssld__content,
.awssld__box {
  display: block;
  position: absolute;
  width: 100%;
  height: 100% !important;
  top: 0;
  left: 0;
}
.awssld__controls button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: var(--control-button-width);
  height: var(--control-button-height);
  position: absolute;
  z-index: 10;
  top: calc(15% - (0.5 * var(--control-button-height)));
  border: none;
  background-color: var(--control-button-background);
  color: #fff;
  cursor: pointer;
}

.awssld__container {
  display: block;
  width: 100%;
  height: 10%;
  padding-bottom: 20% !important;
}

.awssld__bullets .awssld__bullets--active {
  background: #8a4baf !important;
  outline: 2px solid #dec4ec !important;
  outline-offset: 3px !important;
  width: 10px !important;
  height: 10px !important;
}

.awssld__bullets button {
  background: #d9d7e0 !important;
  margin-right: 40px !important;
}

.awssld__bullets button {
  padding: 0;
  display: block;
  width: 12px !important;
  height: 12px !important;
  margin: 5px;
  border-radius: 50%;
  background: var(--control-bullet-color);
  text-indent: -9999px;
  overflow: hidden;
  cursor: pointer;
  border: none;
  transition: transform 0.225s cubic-bezier(0.8, 1.35, 0.75, 1.45),
    background-color 0.175s ease-out;
}

.runing-animation {
  animation: imageSlider linear 16s infinite;
}
.runing-animation:hover {
  -webkit-animation-play-state: paused;
  animation-play-state: paused;
}
.runing-animation img {
  max-width: inherit;
}

@keyframes imageSlider {
  0% {
    transform: translateX(10%);
  }
  100% {
    transform: translateX(-100%);
  }
}

.bg-s-p {
  background-position: center bottom;
  background-repeat: repeat-x;
}
.spend-less {
  position: absolute;
  left: 0;
  top: -30px;
  right: 0;
}
.awssld__bullets {
  z-index: 200;
}
.z-101 {
  z-index: 101;
}
.menu-slide {
  transform: translateX(0%);
  transition: all 0.5s;
}

.content-wrap {
  padding-left: 18rem;
  padding-top: 150px;
}

